import React from 'react'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faMapMarker, faMobileAlt, faAt} from '@fortawesome/free-solid-svg-icons'
import Footer from './Footer';


export default function Contact(){

    function refreshPage() {
        window.location.reload(false);
      }

    return(
        <div>
             <section className="content contact">
            <h2>Kontakt</h2>
            <div className="icons">
                <div className="social">
                    <FontAwesomeIcon className="icon" icon={faMapMarker} />
                    <p>Miasto ul.Ulica 34</p>
                </div>
                <div className="social">
                    <FontAwesomeIcon className="icon" icon={faMobileAlt} />
                    <p>+48 123456789</p>
                </div>
                <div className="social">
                    <FontAwesomeIcon className="icon" icon={faAt} />
                    <p>email@mail.com</p>
                </div>
            </div>
            <form action="">
                <input type="text" placeholder="imię"/>
                <input type="email" placeholder="email"/>
                <input type="text" placeholder="temat"/>
                <textarea name="message" id="" placeholder="wiadomość" cols="30" rows="10"></textarea>
                <button type="submit" className="send" onClick={refreshPage}>Wyślij</button>
            </form>
        </section>
        <Footer />
        </div>
       
    )
}